.perfil-transportista-list-container {
  height: 100%;
  overflow: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.perfil-transportista-list-container .item {
  color: #303ab2;
  display: flex;
  margin-top: 15px;
}

.perfil-transportista-list-container .item span {
  margin-left: 15px;
}

.nombre-cliente {
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 15px;
  color: #303ab2;
}

.boton-calificar {
  background-color: #303ab2;
  border-radius: 5px;
  height: 50px;
  display: flex;
  cursor: pointer;
  color: white;
  justify-content: center;
  align-items: center;
  font-weight: bolder;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
}

.box-info svg {
  position: absolute;
  left: 35px;
}

.calificacion-cliente-container {
  overflow: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.calificacion-cliente-container [type='radio']:checked + label {
  padding: 0;
  position: relative;
  cursor: pointer;
  display: inline-block;
  height: inherit;
  line-height: inherit;
  font-size: inherit;
  transition: 0.28s ease;
  -webkit-user-select: none;
  user-select: none;
}

.calificacion-cliente-container [type='radio'] + label:after {
  display: none;
  font-size: inherit;
}

.calificacion-cliente-container [type='radio']:not(:checked) + label:before {
  display: none;
  border: 0 !important;
  font-size: inherit;
}

.calificacion-cliente-container [type='radio']:not(:checked) + label {
  padding: 0;
  position: relative;
  cursor: pointer;
  display: inline-block;
  height: inherit;
  line-height: inherit;
  font-size: inherit;
  transition: 0.28s ease;
  -webkit-user-select: none;
  user-select: none;
}

.modal-calificacion-transportista {
  --height: 50%;
  --width: 500px;
}
