body {
  margin-top: constant(safe-area-inset-top);
  margin-top: env(safe-area-inset-top);
}

.carousel-indicators li {
  background-color: #fc4c02 !important;
}

.background-white {
  background-color: white !important;
}

.scroll-items-home {
  display: block !important;
  overflow-x: auto;
  white-space: nowrap;
}

.scroll-items-home > .box-producto {
  display: inline-block;
  width: 258px !important;
}

.header .top a,
.header .top a:hover {
  color: var(--ion-color-primary-contrast);
  text-decoration: none;
}

.footer {
  background: #4e4e4e;
}

.footer .top {
  background: #efefef;
  font-weight: bolder;
  color: var(--gris-oscuro-abi);
  min-height: 30px;
}

.footer .top a,
.footer .top a:hover {
  color: var(--gris-oscuro-abi);
  font-weight: bolder;
  text-decoration: none;
  z-index: 1;
}

.banner-principal {
  min-height: 300px;
  background: url(../../assets-ap/images/banner1.jpg) 0 0 no-repeat;
  background-size: cover;
  background-position: center;
  background-color: white;
}

.banner-movil {
  min-height: 80px;
  background: url(../../assets-ap/images/banner1.jpg) 0 0 no-repeat;
  background-size: cover;
  background-position: center;
  background-color: white;
}

.contenedor-titulo-principal {
  background-color: #ffa300;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 15px;
  padding-right: 15px;
}

.titulo-principal {
  font-weight: bold;
  color: var(--ion-color-primary);
}

.contador {
  color: #3f8b0f;
  font-weight: bold;
  font-size: 21px;
}

.box-producto-movil {
  box-shadow: 1px 0 0 0 #e0dbe3, 0 1px 0 0 #e0dbe3 inset, 1px 1px 0 0 #e0dbe3, 0 1px 0 0 #e0dbe3;
}

.categorias-home-swiper {
  width: 100%;
}

.nombre-categoria-carusel {
  height: 1.5rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
  width: auto;
  word-break: break-all;
  text-align: center;
  font-size: 0.7rem;
  line-height: 0.8rem;
  margin-top: 10px;
}

/* Versin de escritorio */
@media screen and (min-device-width: 501px) {
  .nombre-categoria-carusel {
    font-size: 1.125rem;
    line-height: 1.1rem;
  }

  ::-webkit-scrollbar {
    width: 12px;
    height: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-border-radius: 10px;
    border-radius: 10px;
    height: 10px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: #b8b8b8;
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.5);
    height: 10px;
  }

  .titulo-producto-inicio {
    font-size: 16px;
    color: #4e4e4e;
  }

  .sub-titulo-producto-inicio {
    font-size: 16px !important;
    color: #4e4e4e;
    font-weight: bolder;
  }

  .titulo-modo-compra-inicio {
    font-size: 16px;
    color: #4e4e4e;
    margin-bottom: 10px;
    margin-top: 10px;
  }

  .icono-tarjeta-producto {
    height: 26px !important;
  }

  .boton-inicio-productos {
    display: flex;
    flex-grow: 1;
    background-color: #ffa300;
    margin: 0 20px;
    padding: 5px 10px;
    color: #ffffff;
    border-radius: 5px;
    font-weight: bold;
  }

  .boton-inicio-productos:hover {
    background-color: #fc4c02;
  }

  .texto-carrito {
    font-size: 16px;
  }

  .imagen-carrosel-categorias {
    border-radius: 100%;
    height: auto !important;
    width: 200px !important;
    border-color: var(--naranja-abi);
    border-style: solid;
    border-width: 2px;
  }

  .texto-carrito {
    font-size: 12px;
  }

  .imagen-carrosel-categorias {
    border-radius: 100%;
    height: 150px !important;
    width: 150px !important;
    border-color: var(--naranja-abi);
    border-style: solid;
    border-width: 2px;
  }

  .categorias-home-swiper {
    height: 300px;
    background-color: #fff;
  }

  .categorias-home-swiper .swiper-slide:first-of-type {
    margin-left: 0;
  }

  .categorias-home-swiper .swiper-slide:last-of-type {
    margin-right: 0;
  }

  .categorias-home-swiper .swiper-slide {
    margin-top: 10px;
    margin-left: 50px;
    width: 200px;
  }
}

/* Versin movil */
@media screen and (max-device-width: 500px) {
  .imagen-carrosel-categorias {
    border-radius: 100%;
    height: 80px !important;
    width: 80px !important;
    border-color: var(--naranja-abi);
    border-style: solid;
    border-width: 2px;
  }

  .categorias-home-swiper {
    margin-top: -10px;
    height: 150px;
    background-color: #fff;
  }

  .categorias-home-swiper .swiper-slide {
    margin-left: 10px;
    width: 110px;
  }

  #recaptcha-container div div div {
    width: 310px !important;
    background-color: #f4f6f9 !important;
  }

  #recaptcha-container div div div iframe {
    height: 85px !important;
    width: 310px !important;
    background-color: #f4f6f9 !important;
  }

  .titulo-producto-inicio {
    font-size: 16px;
    color: #4e4e4e;
    min-height: 50px;
  }

  .sub-titulo-producto-inicio {
    font-size: 20px;
    color: #4e4e4e;
    font-weight: bolder;
    min-height: 60px;
  }

  .sub-titulo-producto-inicio-precio {
    font-size: 20px;
    color: #4e4e4e;
    font-weight: bold;
  }

  .titulo-modo-compra-inicio {
    font-size: 16px;
    color: #4e4e4e;
    margin-bottom: 8px;
    margin-top: 8px;
  }

  .boton-inicio-productos {
    display: flex;
    flex-grow: 1;
    background-color: #ffa300;
    margin: 0 5px;
    padding: 5px 10px;
    color: #ffffff;
    border-radius: 5px;
    font-size: 13px;
    width: 100%;
    font-weight: bold;
  }

  .boton-inicio-productos svg {
    margin-top: 2px;
  }

  .boton-inicio-productos span {
    font-weight: bolder;
  }
}

.precio-producto {
  font-size: 1.5rem;
  font-weight: bold;
}

.precio-producto-oferta {
  font-size: 22px;
  font-weight: bolder;
  color: #fc4c02;
}

.banner-medio {
  min-height: 100px;
  background: url(../../assets-ap/images/banner2.jpg) 0 0 no-repeat;
  background-size: cover;
  background-position: center;
  background-color: white;
}

.banner-producto {
  min-height: 70px;
  background: url(../../assets-ap/images/banner2.jpg) 0 0 no-repeat;
  background-size: cover;
  background-position: center;
  background-color: white;
}

.modal-menu-estilo .modal-wrapper {
  width: 80%;
  right: 20%;
  position: absolute;
  display: block;
  background-color: #303ab2;
}

.modal-contacto .modal-wrapper {
  width: 300px;
  height: 200px;
  position: absolute;
  display: block;
}

.precio-producto-movil {
  font-size: 18px;
  font-weight: bolder;
  text-decoration: line-through;
  margin-bottom: 0;
}

.precio-producto-oferta-movil {
  font-size: 18px;
  font-weight: bolder;
  color: #fc4c02;
}
.carousel-productos-container {
  background-color: #fff;
  padding-top: 10px;
  width: 100%;
}

.carousel-productos-container-separador {
  width: calc(100% + 40px);
  height: 12px;
  margin-left: -20px;
  margin-right: -20px;
  background: #efefef;
  margin-top: 10px;
}



.loader .loadingSpinner {
  margin-top: 10em;
  width: 10%;
  height: 10%;
  position: absolute;
  left: calc(50% - 10% / 2);
  color: #303ab2;
}

.border-box-precio {
  border: 1px solid #e0dbe3 !important;
}

.input-position {
  width: 50%;
  position: relative;
}

.swiper-button-next {
  color: var(--gris-claro-abi);
  padding-left: 10px;
}

.swiper-button-next::after {
  color: #4e4e4eff;
}

.swiper-button-prev {
  color: var(--gris-claro-abi);
}

.swiper-button-prev::after {
  color: #4e4e4eff;
  padding-right: 10px;
}

.carrito-tarjeta {
  color: var(--gris-claro-abi);
}

.carrito-tarjeta:hover {
  color: var(--gris-oscuro-abi);
}

.swiper-button-prev, .swiper-container-rtl .swiper-button-next {
  left: -25px;
}

.swiper-button-next, .swiper-container-rtl .swiper-button-next {
  right: -25px;
}

.footer-movil {
  font-size: x-small;
  font-weight: bolder;
}

.modal-favoritos {
  --width: 60%;
  --border-radius: 15px;
}

.carousel-marcas-container .btn-unirse {
  height: 210px;
}

.carousel-marcas-container .arrow-container, .carousel-marcas-container .btn-unirse {
  height: 100%;
  display: flex;
  align-self: stretch;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.carousel-marcas, .carousel-marcas > div {
  height: 100%;
}
