.modal-registro-fabricante #contenido-registro {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  background-color: rgb(255, 255, 255);
}

.modal-registro-fabricante #contenido-registro .formulario-datos-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
}

.modal-registro-fabricante .titulo-container {
  text-align: center;
}

.modal-registro-fabricante .titulo {
  font-weight: bold;
  font-size: 1.5rem;
  color: var(--azul-abi);
  padding-top: 1rem;
}

.modal-registro-fabricante .subtitulo {
  font-weight: 400;
  font-size: 1.125rem;
  color: var(--gris-oscuro-abi);
}

#pasos-registro .horario-atencion-container h4 {
  text-align: center;
  color: var(--gris-oscuro);
}

#pasos-registro .form-control .registro {
  border-top: none;
  border-left: none;
  border-right: none;
}

.modal-registro-fabricante {
  --border-radius: 20px;
  --min-width: 1039px;
  --max-width: 1039px;
}

.modal-registro-fabricante .direccion-icon-info svg {
  width: 25px;
  height: 25px;
}

.modal-registro-fabricante .input-rubro .row {
  flex-grow: 1;
}

.modal-registro-fabricante .input-rubro .row .input-group {
  margin-bottom: 0 !important;
}

.modal-registro-fabricante .btn-atras {
  position: absolute;
  left: 15px;
  top: 15px;
  cursor: pointer;
  z-index: 100;
}

.modal-registro-fabricante .input-ciudad .input-group {
  margin-bottom: 0 !important;
}

.modal-registro-fabricante .input-ciudad .registro-selector-modal-input,
.modal-registro-fabricante .input-ciudad .btn-registro-selector-modal {
  height: 40px !important;
}

.modal-registro-fabricante .input-hora {
  display: flex;
  flex-direction: row;
  color: var(--gris-oscuro-abi);
  font-size: 2rem;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.modal-registro-fabricante .input-hora input {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  text-align: center;
  color: var(--gris-oscuro-abi);
  font-size: 2rem;
  flex-shrink: 0;
  font-weight: bold;
  background-color: #f9f9f9;
}

.modal-registro-fabricante .footer-hora {
  background-color: #f9f9f9 !important;
  margin: 0 !important;
}

.modal-registro-fabricante .dia-apertura {
  font-size: 1.25rem;
  font-weight: bold;
  padding: 0.625rem;
}

.modal-registro-fabricante .paso-registro-container {
  overflow-y: auto;
}

.modal-registro-fabricante .btn-preguntas,
.modal-registro-fabricante .btn-contacto {
  text-align: center;
}

.modal-registro-fabricante .btn-preguntas svg,
.modal-registro-fabricante .btn-contacto svg {
  width: 25px;
  height: 25px;
}

.modal-registro-fabricante .btn-preguntas,
.modal-registro-fabricante .btn-preguntas svg path,
.modal-registro-fabricante .btn-contacto,
.modal-registro-fabricante .btn-contacto svg path {
  color: var(--azul-fabricante);
  fill: var(--azul-fabricante)!important;
}

@media screen and (min-width: 501px) {
  #pasos-registro {
    height: 100%;
  }
  .modal-registro-fabricante .pasos-registro-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin: 0px;
    height: 100%;
  }
  .modal-registro-fabricante .paso-registro-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
  }
}

@media screen and (min-width: 1000px) {
  #pasos-registro {
    height: 100%;
  }
  .modal-registro-fabricante .pasos-registro-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin: 0px;
    height: 100%;
  }
  .modal-registro-fabricante .paso-registro-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
  }
}

@media screen and (max-width: 500px) {
  .modal-registro-fabricante {
    --border-radius: 0;
    --min-width: 100%;
    --max-width: 100%;
  }

  .modal-registro-fabricante .paso-direccion {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-self: stretch;
    justify-content: space-between;
  }
}
