.chat-container {
  min-width: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
}

#respuesta-container {
  flex: 0 0 1;
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: 6px;
  background: #f7f7f7;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  position: relative;
}

#mensajes-container {
}
#mensajes-container > div #mensajes {
  display: flex;
  flex-direction: column;
}
#respuesta-container .adjunto {
  padding: 3px;
  border: 1px solid #ccc;
  border-radius: 100%;
  width: 40px;
  height: 40px;
  align-self: flex-end;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

#respuesta-container .enviar {
  border: 1px solid #96ce4e;
  border-radius: 100%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

#respuesta-container .input-texto {
  border: 0;
  align-self: stretch;
  flex-grow: 1;
  margin: 0 4px;
  background: none;
  overflow-y: scroll;
}

.header-container {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.foto-perfil-container {
  flex-grow: 1;
}
img#foto-perfil {
  z-index: 99999;
  height: 50px;
  width: 50px;
  position: absolute;
  top: 50px;
  left: 40px;
  border-radius: 50%;
  background-color: #303ab2 !important;
  object-fit: cover;
}

#titulos-container {
  z-index: 9999;
}

#titulo {
  flex-grow: 2;
  font-size: large;
  font-weight: bold;
  align-self: center;
  text-align: center;
}

.boton-menu {
  z-index: 99999;
  position: absolute;
  top: 65px;
  right: 15px;
  display: none;
}

.chat-page .boton-menu-20,
.chat-page .boton-menu-30 {
  display: block;
}

.corte-fecha {
  align-self: center;
  padding: 15px 15px 15px 15px;
  margin-bottom: 20px;
  border-radius: 15px;
  background: #f7f7f7;
}

.mensaje {
  border: 1px solid #d9d9db;
  padding: 15px 15px 25px 15px;
  position: relative;
  align-self: flex-start;
  min-width: 200px;
  max-width: 65%;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  margin-bottom: 20px;
  margin-left: 20px;
}

.mensaje img {
  object-fit: contain;
}

.mensaje.ubicacion {
  padding: 2px 2px 25px 2px;
}

.mensaje.propio {
  border: 1px solid #f7f7f7;
  background: #f7f7f7;
  align-self: flex-end;
  margin-left: 0;
  margin-right: 20px;
  border-radius: 15px 15px 0 15px;
}

.mensaje > svg {
  position: absolute;
  left: -14px;
  bottom: -4.9px;
  width: 20px;
  height: 20px;
}

.mensaje.propio > svg {
  position: absolute;
  left: auto;
  right: -14px;
  bottom: -8px;
  width: 20px;
  height: 20px;
  transform: rotate(220deg);
}

.mensaje.propio > svg .cls-1 {
  fill: #f7f7f7;
  stroke: #f7f7f7;
}

.mensaje .hora {
  position: absolute;
  bottom: 4px;
  right: 14px;
  font-size: 13px;
  font-weight: 400;
}

.mensaje.propio .hora {
  right: auto;
  left: 14px;
}

.cls-1 {
  fill: white;
  stroke: #d9d9db;
  stroke-width: 0.9px;
  stroke-miterlimit: 10;
}

.predeterminados-container {
  height: 10vh;
  background-color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  overflow-y: scroll;
  justify-content: center;
  border-top: 1px solid #f0f0f0;
  padding-top: 5px;
}

.predeterminados-container-trasportista {
  height: 40vh;
  background-color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  overflow-y: scroll;
  justify-content: center;
  border-top: 1px solid #f0f0f0;
  padding-top: 5px;
}

.mensaje-predeterminado-container {
  width: 50%;
  margin-bottom: 5px;
  text-align: center;
  border-radius: 10%;
}

.mensaje-predeterminado-trasportista {
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 5px;
  background-color: #f7f7f7;
  border-radius: 5%;
  user-select: none;
}

.mensaje-predeterminado-cliente {
  flex-grow: 1;
  flex-shrink: 0;
  margin-left: 5px;
  margin-bottom: 5px;
  padding: 10px;
  width: fit-content;
  background-color: #f7f7f7;
  align-self: flex-start;
  margin-bottom: 10px;
  margin-top: 5px;
  cursor: pointer;
  user-select: none;
}

#cliente-predeterminados-container {
  border-top: 1px solid #f0f0f0;
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
}

.chat-page .menu-bar-fabricante-aprobado {
  height: 110px !important;
}

#menu-adjuntar {
  position: absolute;
  bottom: 70px;
  left: 10px;
  display: none;
  background-color: #f7f7f7;
  border-radius: 60px;
}

#menu-adjuntar.active {
  display: block;
}

#adjuntar-ubicacion {
  border: 1px solid #ff4b00;
  padding: 5px;
  border-radius: 100%;
  cursor: pointer;
}

#adjuntar-ubicacion svg {
  color: #ff4b00;
}

#adjuntar-foto {
  border: 1px solid #2737b6;
  padding: 5px;
  border-radius: 100%;
  margin-bottom: 5px;
  cursor: pointer;
}

#adjuntar-foto svg {
  color: #2737b6;
}

#adjuntar-imagen {
  border: 1px solid #ffa200;
  padding: 5px;
  border-radius: 100%;
  margin-bottom: 5px;
  cursor: pointer;
}

#adjuntar-imagen svg {
  color: #ffa200;
}

.modal-ubicacion .modal-wrapper {
  background: #fff;
}

#enviar-ubicacion {
  position: absolute;
  bottom: 2px;
  right: 2px;
  z-index: 999999;
}

#chat-loading-spinner {
  width: 60px;
  height: 60px;
  color: #303ab2;
  align-self: center;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -30px;
  margin-left: -30px;
}

.chat-loading-container {
  display: flex;
  flex-grow: 1;
  align-self: stretch;
  justify-content: center;
  align-items: center;
}
