#modal-selector-modo {
    --width: 1000px;
    position: absolute;
    z-index: 999;
}

#modal-selector-modo .titulo {
    font-weight: bold;
    font-size: 2rem;
    color: var(--azul-abi);
    padding-top: 1.5rem;
}

#modal-selector-modo .subtitulo {
    font-weight: 400;
    font-size: 1.25rem;
    color: var(--gris-oscuro-abi);
}

#modal-selector-modo .modos {
    padding: 0 4rem;
}

#modal-selector-modo .titulo-container {
    text-align: center;
    padding: 1.25rem 0;
}

#modal-selector-modo .mfooter {
    color: var(--gris-oscuro-abi);
    text-align: center;
    font-weight: 400;
    padding: 1.25rem 0;
    background-color: #efefef;
}

#modal-selector-modo .login-btn {
    font-weight: bold;
    font-size: 1.25rem;
    color: var(--azul-abi);
    cursor: pointer;
}

#modal-selector-modo .btn-cerrar {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
}

#modal-selector-modo .ion-page {
    overflow-y: auto;
}

@media screen and (min-width: 501px) {
    #modal-selector-modo {
        --border-radius: 20px;
    }
}
