.margen-texto-registro-transportista {
  margin-left: 1.5rem;
}

.icono-registro-trasportista {
  height: 28px;
  margin-right: 5px;
  margin-left: 5px;
}

.btn-pricipal-registro-transportista {
  color: #fff;
  background-color: var(--rojo-abi);
  border-color: var(--rojo-abi);
  border-radius: 4px;
}

.inicio-registro-wraper-transportista {
  padding-left: 0 !important;
  padding-right: 0 !important;
  padding-bottom: 0 !important;
}

.footer-registro-transportista {
  margin-top: 10px;
  width: 100%;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15px;
  justify-content: flex-start;
  background-color: #efefef;
}

.texto-info-registro-transportista {
  font-size: 12px !important;
}

.info-direccion-transportista {
  --height: 100px;
}

.paso-registro-transportista-container {
  height: calc(100vh - 150px);
  overflow-y: auto;
  background: #efefef;
  padding-top: var(--ion-safe-area-top);
  padding-bottom: var(--ion-safe-area-bottom);
}

@media (min-width: 600px) {
  #modal-registro-transportista .modal-wrapper {
    height: 95vh;
  }
  .paso-registro-transportista-container {
    height: calc(95vh - 150px);
    overflow-y: auto;
  }
}

.campo-registro-transportista .registro-selector-modal-input {
  padding: 0.375rem 0.75rem;
  color: #afafaf;
}

.campo-registro-transportista .registro-selector-modal-input::placeholder, .campo-registro-transportista input,
.campo-registro-transportista textarea {
  color: #afafaf;
}

.campo-registro-transportista.select-modal .registro-selector-modal-input {
  border-left: 0;
  border-top: 0;
  font-size: 1rem!important;
}

.campo-registro-transportista.select-modal button {
  border-right: 0;
  border-top: 0;
}

.checkbox-disabled {
  opacity: 1;
}

@media (min-width: 768px){
  #modal-registro-transportista ~ .sc-ion-modal-md-h {
    --height: 95%;
  }
}
