
.modal-numpad {
    font-size: 20px;
}

.numpad-maindiv {
    font-size: 30px;
    font-weight: bold;
    height: 16%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.numpad-key {
 height: 16%;

display: flex;
justify-content: center;
align-items: center;
}

.numpad-container {
    width: 80%;
    height: 80%;
    position: absolute;
    left: calc((100% - 80%) / 2);
    top: calc((100% - 80%) / 2);

    display: flex;
    flex-wrap: wrap;
}