.label-selector-modal-registro {
    color: #4e4e4e;
    font-weight:  bolder;
    font-size: 15px;
}

.registro-selector-modal-input {
    background-color: white;
    font-size: 14px!important;

}

.registro-selector-modal-input:disabled, .registro-selector-modal-input[readonly] {
    background-color: white!important;
    border-color: #afafaf;
    padding: 0 5px;
    height: 30px!important;
    border-right: 0!important;
}

.btn-registro-selector-modal{
    border-color: #afafaf!important;
    height: 30px!important;
    border-left: 0!important;
    line-height: 0!important;
}
