.oportunidades-header {
  height: 60px;
  width: 100%;
  position: relative;
  display: flex;
  flex-shrink: 0;
  align-items: center;
}

.oportunidades-header > svg {
  margin-left: 15px;
}

.oportunidades-header > span {
  font-weight: bolder;
  font-size: 25px;
  color: #ffffff !important;
  margin-left: 20px;
}

.oportunidades-list-container {
  height: fit-content;
  max-height: 50%;
  overflow-x: scroll;
  /* position: relative; */
}

.oportunidades-list-container .loadingSpinner {
  margin-top: 6em;
  width: 20%;
  height: 20%;
  position: absolute;
  left: calc(50% - 20% / 2);
}
.oportunidades-list-container h4 {
  margin-bottom: 20px;
}

.oportunidades-list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
  font-size: 20px;
  cursor: pointer;
  border-bottom: 3px solid var(--gris-claro);
}

.oportunidades-list-item * {
  color: var(--gris-oscuro-abi);
}

.oportunidades-list-item .item-body {
  margin: 0.5em 0 0.5em 0;
}

.oportunidades-list-item .item-header {
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.oportunidades-list-item .item-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.oportunidades-list-item p {
  margin-bottom: 0.7rem;
}
.oportunidades-list-item .datos-punto-itinerario * {
  color: var(--gris-oscuro-abi);
  font-size: 0.7rem;
}

#oportunidades-page-content .oportunidades-list-item .datos-punto-itinerario {
  font-size: 0.7rem;
  font-weight: lighter;
  color: var(--gris-oscuro-abi);
}

#oportunidades-page-content h4 {
  color: var(--gris-oscuro-abi);
}

.oportunidades-list-item .datos-punto-itinerario img {
  height: 15px;
  width: 15px;
  margin-right: 10px;
}

.oportunidades-list-item .datos-punto-itinerario {
  line-height: 1rem;
}

.datos-empresa p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 250px;
  font-size: 0.8rem;
}

.datos-empresa strong {
  font-size: 0.9rem;
  color: var(--gris-oscuro-abi);
}

.datos-empresa strong.wrap {
  max-width: 63vw;
  word-wrap: anywhere;
  display: inline-block;
  -webkit-line-clamp: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

img.icono-ver-mapa {
  height: 60px;
  width: 60px;
}

#oportunidades-page-content {
  width: 100%;
  height: 100%;
}

.oportunidad-iniciar {
  z-index: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 75px;
  position: absolute;
  width: 100%;
}

.oportunidad-iniciar > div {
  background-color: rgba(255, 255, 255, 0.8);
  border-bottom: 2px solid var(--naranja-transportista);
  width: 98vw;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 1vh;
  border-top-left-radius: 1vh;
  border-top-right-radius: 1vh;
  display: flex;
  align-items: center;
}

#oportunidades-page-content .map-container {
  width: 100%;
  height: 40%;
  display: grid;
  grid-template-rows: 1fr 150px 50px;
}

#oportunidades-page-content .map-container .leaflet-container {
  grid-area: 1/1/4/1;
}
.map-container .oportunidad-iniciar {
  z-index: 1000;
  grid-area: 2/1/3/1;
}

.opotunitades-entrega-dropdown {
  z-index: 1000;
  grid-area: 3/1/4/1;
}

.map-container .oportunidad-iniciar {
  z-index: 1000;
  grid-area: 2/1/3/1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.map-container .oportunidad-iniciar .oportunidades-list-item {
  padding-bottom: 1vh;
  padding-left: 5px;
  padding-right: 5px;
}
