.modal-side-menu-estilo .modal-wrapper {
  width: 75%;
  right: 25%;
  border-top-right-radius: 20px;
  position: absolute;
  display: block;
  opacity: 0.8;
  background-color: #fff;
}
.modal-side-menu-estilo {
  --backdrop-opacity: var(--ion-backdrop-opacity, 0.2);
}
.drop-side-menu {
  display: flex;
  flex-direction: column;
  color: var(--gris-oscuro-abi);
  gap: 17px;
  margin-top: 20px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  font-weight: bolder;
  height: 120px;
  overflow-y: scroll;
  border-bottom: 1px solid var(--gris-claro-abi);
}
.imagen-perfil-side-menu {
  height: 60px;
  width: 60px;
  object-fit: fill;
}
