.header-container {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  /* border: 2px solid #ffa100; */
}
#titulo {
  flex-grow: 2;
  font-size: large;
  font-weight: bold;
  align-self: center;
  text-align: center;
}
.foto-perfil-container-chat-modal {
  height: 60px;
  width: 60px;
  z-index: 99999;
  background: white;
  border-radius: 100%;
  overflow: hidden;
  border: 2px solid #ffa100;
}

.foto-perfil-container-chat-modal img {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  background-color: #303ab2 !important;
  object-fit: cover;
}
