.boton-header-cliente {
  margin-right: 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 2px;
}

.boton-header-cliente:hover .texto-header {
  color: #CCC !important;
}

.boton-header-cliente:hover svg path {
  fill: #CCC !important;
}

.imagen-header {
  height: 30px;
}

.header .top-cliente {
  color: var(--ion-color-primary-contrast);
  flex-grow: 1;
  min-height: 60px;
  display: grid;
  grid-template-columns: 1fr 1.7fr;
}
.top-cliente-movil {
  color: var(--ion-color-primary-contrast);
  height: 68px;

  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  flex-wrap: nowrap;
  align-items: center;
  flex-direction: row;
  padding-left: 10px;
  padding-right: 10px;
}
.toggle-principal-categoria-header {
  background-color: #e0dbe2 !important;
  color: var(--gris-oscuro-abi) !important;
  border: none !important;
}
.toggle-principal-categoria-header:hover {
  background-color: var(--naranja-abi) !important;
  color: var(--gris-oscuro-abi) !important;
  border: none !important;
}
.toggle-secundario-categoria-header {
  padding: 0px;
  background-color: red;
  color: var(--gris-oscuro-abi) !important;
  border: none !important;
}
.toggle-secundario-categoria-header:hover {
  background-color: var(--naranja-abi) !important;
  color: var(--gris-oscuro-abi) !important;
  border: none !important;
}

.logo-no-direccion-header-movil,
.logo-no-direccion-header-movil > a {
  width: 42px;
}

.bloque-direccion .texto-direccion {
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.8rem;
  word-wrap: anywhere;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-height: 0.7rem;
}

.nombre-usuario-header {
  max-width: 90px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.75rem;
  font-weight: bold;
  line-height: 0.8rem;
  text-align: right;
  word-wrap: anywhere;
}

@media (max-width: 720px) {
  .btn-whatsapp img {
    width: 45px !important;
    margin-bottom: 27px !important;
    margin-left: 4px;
    padding-top: 8px;
  }

  .btn-whatsapp {
    width: 75px !important;
    height: 75px !important;
    right: 10px !important;
    bottom: 105px !important;
  }
}

.btn-whatsapp {
  display: block;
  width: 80px;
  height: 80px;
  color: #fff;
  position: fixed;
  right: 40px;
  bottom: 40px;
  background: rgba(236, 236, 236, 0.5);
  border: 1px solid #ececec;
  border-radius: 50%;
  line-height: 80px;
  text-align: center;
  z-index: 999999;
}

.btn-whatsapp img {
  width: 50px;
  margin-bottom: 3px;
}

#mainmenu.menu {
  display: block;
  z-index: 999999;
  margin-bottom: 0;
  padding: 0;
}
#mainmenu.menu li {
  position: relative;
  list-style: none;
}
#mainmenu.menu li.dropdown:hover > .submenu {
  display: block;
}
#mainmenu.menu li a {
  display: block;
  padding: 6px;
  background: none;
}
#mainmenu.menu li a:hover {
  background: var(--naranja-abi);
}

#mainmenu.menu .submenu {
  display: none;
}
#mainmenu .submenu {
  margin: 0px;
  padding: 0px;
  position: absolute;
  left: 0px;
  list-style-type: none;
  display: block;
  width: 235px;
  z-index: 9;
  background: rgba(224, 219, 226, 0.9);
}
#mainmenu .submenu li {
  display: block;
  line-height: 1.125rem;
}
#mainmenu .submenu li a {
  display: block;
  color: var(--gris-oscuro-abi);
  padding: 8px 12px;
  margin-bottom: 1px;
}
#mainmenu.menu .submenu > li.dropdown:hover > .submenu {
  display: block;
}

#mainmenu.menu .submenu .submenu {
  position: absolute;
  left: 235px;
  top: 0px;
}

.icon-categoria {
  width: 25px;
  height: 25px;
  margin-right: 10px;
  margin-left: 10px;
}
