.registro-cliente-header-main-container {
  position: fixed !important;
  height: 100px;
  width: 100%;
  z-index: 400;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.logo-abi-fabricante svg {
  width: 30px;
  height: 30px;
}

.logo-abi-fabricante svg path {
  fill: white !important;
}

.first-card-fabricante {
  margin-top: 10px;
}

.menu-bar-fabricante {
  width: 100%;
  z-index: 400;
}

.fabricante-header {
  height: 60px;
  width: 100%;
  position: relative;
  display: flex;
  flex-shrink: 0;
  align-items: center;
}

.fabricante-header > svg {
  margin-left: 15px;
}

.fabricante-header > span {
  font-weight: bolder;
  font-size: 25px;
  color: #ffffff !important;
  margin-left: 20px;
}

.loadingSpinnerFabricanteApp {
  margin-top: 6em;
  width: 20%;
  height: 20%;
  position: absolute;
  left: calc(50% - 20% / 2);
  color: #303ab2 !important;
}

.boton-app-editar {
  background-color: white;
  color: #303ab2;
  display: flex;
  border-radius: 5px;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-weight: bolder;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
  padding: 15px 0;
  position: relative;
  width: 100%;
}

.boton-app-editar svg {
  position: absolute;
  left: 35px;
}

.content-editable-div {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #54667a;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #e9ecef;
  border-radius: 4px;
}
.banner-fabricante-home {
  background-size: cover;
  background-position: center;
  background-color: white;
  height: 120px !important;
}

.extra-margin-top {
  margin-top: 140px;
}

.preguntas-frecuentes-title {
  color: #4a4d50;
  margin-top: 55px;
  margin-bottom: 10px;
}

.buscador-container-preguntas-frecuentes {
  color: #dddae3 !important;
}

.buscador-container-preguntas-frecuentes .form-control {
  height: 30px !important;
}

.buscador-container-preguntas-frecuentes .input-group-append {
  height: 30px !important;
}

.buscador-container-preguntas-frecuentes .input-group-append .btn {
  line-height: 0;
  color: #4a4d50 !important;
}

.buscador-container-preguntas-frecuentes .form-control::placeholder {
  font-size: 12px;
  color: #dddae3;
}

.fondo-pagina {
  background-size: 100% auto;
  background-position: bottom;
  background-repeat: no-repeat;
}

.proveedores-container {
  background-size: 250px 250px;
  background-position: center 250px;
  background-repeat: no-repeat;
}
.proveedores-container-aprobado {
  width: 100%;
  overflow-y: scroll;
}

.menu-bar-fabricante-aprobado {
  background-color: #303ab2;
  width: 100%;
  z-index: 900;
  position: absolute;
  top: 0;
  left: 0;
}

.proveedores-container .texto-1 {
  margin: 90px 50px 20px 50px;
  font-size: 14px;
  text-align: center;
}

.proveedores-container .texto-1 > img {
  width: 18px;
  height: 18px;
  margin-right: 5px;
}

.proveedores-container .texto-2 {
  font-size: 14px;
  text-align: center;
}

.image-container-header-home {
  position: relative;
}

.image-container-header-home > img {
  width: 40px;
  height: 40px;
}

.image-container-header-home > div {
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #303ab2;
  bottom: -7px;
  right: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container-header-home > div > span {
  color: #ffa300;
  font-size: 14px;
  font-weight: bolder;
}

.menu-bar-aprobado {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  border-top-width: 1px;
  border-color: white;
  border-style: solid;
}

.menu-bar-aprobado > img {
  width: 35px;
  height: 35px;
  margin: 5px 10px 0 10px;
}

.menu-bar-aprobado svg {
  width: 25px;
  height: 25px;
  fill: #fff;
  margin-right: 10px;
  margin-left: 10px;
}

.menu-bar-aprobado .chico {
  width: 30px;
  height: 30px;
  margin: 5px 10px 0 10px;
}

.title-home-aprobado {
  color: #4a4d50;
  margin-top: 15px;
  margin-bottom: 10px;
  font-size: 13px;
  position: relative;
}

.title-home-aprobado > img {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 50px;
}

.carrusel-productos-fabricantes {
  display: block !important;
  overflow-x: auto;
  white-space: nowrap;
}

.carrusel-productos-fabricantes > div {
  border: 1px solid #f7f7f7;
  margin: 0 2px;
  border-radius: 5px;
  display: inline-block;
  padding: 0;
}
.carrusel-productos-fabricantes > div > .image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.block-container {
  box-shadow: 5px 0 0 0 #f7f7f7, 0 5px 0 0 #f7f7f7, 5px 5px 0 0 #f7f7f7;
  padding-bottom: 10px;
}

.agregar-producto {
  width: 100px;
  height: 100px;
  align-self: center;
}

.text-gray {
  color: #4e4e4e !important;
}

.imagen-perfil-editar-container {
  text-align: center;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 2px solid #303ab2;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icono-imagen-perfil-fabricante {
  position: absolute;
  width: 40px;
  height: 40px;
  left: 130px;
  top: 110px;
}

.container-flecha-mapa {
  position: relative;
}

.flecha-mapa-atencion {
  position: absolute;
  top: 260px;
  right: 10px;
  z-index: 9999;
  background-color: #303ab2;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
