.boton-photo-loader-registro {
  display: flex;
  flex-grow: 1;
  margin: 0;
  padding: 5px 10px;
  cursor: pointer;
  font-weight: bolder;
  font-size: 15px;
}

.boton-photo-loader-registro > img {
  width: 25px;
  height: 25px;
}
.info-image {
  width: 25px;
  height: 25px;
}

.circulo-contenedor-imagen-registro {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 2px solid #303ab2;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  overflow: hidden;
}

.circulo-contenedor-imagen-registro > .sin-imagen {
  width: 90px;
  height: 90px;
  border-radius: 50%;
}

.circulo-contenedor-imagen-registro > .con-imagen {
  width: 148px;
  height: 148px;
  object-fit: cover;
  border-radius: 50%;
}

.boton-photo-loader span {
  text-align: center;
}

.helptext-div {
  flex: 0 1 auto;
}

.helptext-img-div {
  flex: 0 1 auto;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.helptext-img-div-img {
  height: 100%;
}

.helptext-img-div-img-smaller {
  height: 70%;
}

.photoload-content-container {
  display: flex;
  flex-flow: column;
}

.preview-imagen-item {
  max-height: 250px;
}

.requisitos-foto-perfil {
  --height: 230px;
}

@media all and (max-width: 499px) {
  .requisitos-foto-perfil {
    --height: 220px;
  }
}
@media all and (min-width: 500px) {
  .requisitos-foto-perfil {
    --height: 230px;
  }
}
