.footer-home-transportista {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  z-index: 400;
  background-color: #fff;
  font-size: 20px;
  font-weight: bold;
}

.footer-home-transportista .buscando-oportunidad > strong {
  font-size: 0.8rem;
}

.footer-home-transportista p {
  color: var(--gris-oscuro-abi);
  font-weight: lighter;
}

.footer-home-transportista .buscando-oportunidad svg {
  color: var(--gris-oscuro-abi);
}

.footer-home-transportista.open {
  height: 250px;
}

.footer-home-transportista.closed {
  height: 75px;
}

.footer-home-transportista > div {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-home-transportista-icon-open {
  position: absolute;
  left: 5px;
  bottom: 12px;
}

.footer-home-transportista-icon-close {
  position: absolute;
  left: 30px;
  top: 5px;
}

.footer-home-transportista-icon-info {
  position: absolute;
}

.footer-home-transportista-icon-info.open {
  top: 5px;
  right: 20px;
}

.footer-home-transportista-icon-info.closed {
  bottom: 12px;
  right: 20px;
}

.footer-open-row {
  margin-left: 10px !important;
}

.imagen-entregar-pedido {
  width: 100px;
  height: 100px;
}
