
div#sidebar-fabricante.left-sidebar {
  position: static !important;
  height: 100%;
  box-shadow: none;
  background-color: white !important;
}

#sidebar-fabricante nav.sidebar-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3vh;
  position: -webkit-sticky;
  position: sticky;
  top: 170px;
}

#sidebar-fabricante nav.sidebar-nav > a {
  display: flex;
  justify-content: flex-start;
  align-self: stretch;
  border-radius: 0.2rem;
  border: 0.1rem solid var(--gris-oscuro);
  color: var(--gris-oscuro);
  font-weight: bold;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 0.5rem;
}

#sidebar-fabricante nav.sidebar-nav > a.resaltado,
#sidebar-fabricante nav.sidebar-nav > a:hover {
  color: #fff;
  background-color: var(--azul-fabricante);
  border-color: var(--azul-fabricante);
}

#sidebar-fabricante nav.sidebar-nav > a.resaltado-naranja,
#sidebar-fabricante nav.sidebar-nav > a.resaltado-naranja:hover {
  color: #fff;
  background-color: var(--naranja-transportista);
  border-color: var(--naranja-transportista);
}

#sidebar-fabricante nav.sidebar-nav > a.resaltado svg path,
#sidebar-fabricante nav.sidebar-nav > a:hover svg path {
  fill: #FFF !important;
}

#sidebar-fabricante nav.sidebar-nav > a.resaltado > img,
#sidebar-fabricante nav.sidebar-nav > a:hover > img {
  filter: brightness(0) invert(1);
}

#sidebar-fabricante nav.sidebar-nav > a > img, #sidebar-fabricante nav.sidebar-nav > a svg {
  height: 1.5rem;
  width: 1.5rem;
  margin-right: 0.5rem;
}

#sidebar-fabricante .txt-dotsidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
}

#sidebar-fabricante .txt-dotsidebar .dot {
  width: 15px;
  height: 15px;
  border-radius: 15px;
  background-color: #fe4b00;
  margin-right: 10px;
}