.fecha-set-checkout {
  font-size: 0.8rem;
}

.direccion-checkout-carrito {
  display: flex;
}

.icono-direccion-tarjeta-container {
  min-width: 30px;
  height: 30px;
}

.icono-direccion-checkout {
  height: 20px;
  margin-right: 10px;
}

.modal-info-metodo-pago .metodos-pago-container {
  height: 50vh;
  display: flex;
  flex-direction: column;
}
.modal-info-metodo-pago .metodos-pago-container .item-medio-pago {
  display: flex;
  margin-top: 1rem;
  align-items: flex-start;
}
.modal-info-metodo-pago .metodos-pago-container .item-medio-pago .checkbox {
  width: 2vw;
  min-width: 40px;
}
.modal-info-metodo-pago .metodos-pago-container .item-medio-pago .checkbox ion-checkbox {
  --border-radius: 100%;
  --background-checked: white;
  --border-color-checked: var(--verde-claro);
  --checkmark-color: var(--verde-claro);
}

.modal-info-metodo-pago .metodos-pago-container strong {
  font-size: 0.8rem;
  max-width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
}

.modal-info-metodo-pago .metodos-pago-container p.help {
  font-size: 0.7rem;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-bottom: 0;
}

.modal-info-metodo-pago .metodos-pago-container button, .modal-compra-programada button  {
  font-size: 0.8rem;
}

@media screen and (max-device-width: 599px) {
  .icono-direccion-checkout {
    height: 20px;
    margin-right: 5px;
  }
  .modal-info-metodo-pago {
    --max-height: 50vh;
  }

  .modal-info-metodo-pago .metodos-pago-containe button {
    font-size: 0.8rem;
  }
}

@media screen and (min-device-width: 600px) {
  .icono-direccion-tarjeta-container {
    min-width: 50px;
    height: 30px;
  }
  .icono-direccion-checkout {
    height: 20px;
    margin-right: 10px;
  }
  .modal-info-metodo-pago {
    --min-height: 450px;
    --max-height: 500px;
  }
}

.modal-compra-programada ion-input, .modal-compra-programada input, .modal-compra-programada span {
  font-size: 1rem !important;
}

.modal-compra-programada {
  --height:25rem !important;
  --min-height: 320px !important;
}

