.modal-registro-abi {
  --border-radius: 20px;
  --min-width: 1000px;
  --height: 540px;
  --border-width: 0px;
  --backdrop-opacity: 0.7;
}

.modal-info-registro {
  --height: 200px;
  --width: 320px;
  --border-radius: 5px;
  --backdrop-opacity: 0.7;
}
.modal-info-registro-short {
  --height: 100px;
  --width: 320px;
  --border-radius: 5px;
  --backdrop-opacity: 0.7;
}
.heather-info-registro {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-weight: bolder;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 0px;
  padding-left: 10px;
}
.centrado-texto-flex {
  width: 100%;
  display: flex;
  justify-content: center;
}
.justificar-inicio-flex {
  width: 100%;
  display: flex;
  justify-content: flex-start;
}
.foto-perfil-loader-small {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid #303ab2;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.foto-perfil-loader-normal {
  width: 150px;
  height: 150px;
}

.preview-foto-loader-registro {
  object-fit: cover;
}

.icono-info-registro {
  height: 25px;
  width: 25px;
  margin: 5px;
}

.banner-registro-cliente {
  overflow: hidden;
  width: 530px;
  height: 512px;
  background: url(../../assets-ap/images/banner-registro-cliente.jpg) 0 0 no-repeat !important;
  background-size: cover;
  background-position: center;
  background-color: #ffa300;
}

.modal-registro-fabricante .banner-fabricante {
  overflow: hidden;
  width: 520px;
  height: 520px;
  background: url(../../assets-ap/images/banner-registro-fabricante.png) 0 0 no-repeat !important;
  background-size: cover;
  background-position: center;
  background-color: var(--azul-fabricante);
}

.derecha-container-registro-abi {
  width: 520px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.izquierda-container-registro-abi {
  flex-grow: 1;
  height: 100%;
  max-width: 552px;
  flex-direction: column;
  overflow-y: auto;
  text-align: center;
  background: #efefef;
}
/* 
.izquierda-container-registro-abi div, .izquierda-container-registro-abi h6 {
  background: #FFF;
} */

.icono-direccion-seleccionado {
  padding: 10px;
  border-radius: 5px;
  background-color: #ffa300 !important;
  cursor: pointer;
}

.icono-direccion {
  padding: 10px;
  border-radius: 5px;
  filter: grayscale(100%);
  cursor: pointer;
}

.filter-invert-imagen {
  filter: brightness(0) invert(1);
}

.titulo-login-abi {
  color: #ffa300;
  font-weight: bold;
  user-select: none;
}
.sub-titulo-login-abi {
  color: #4e4e4eff;
  font-weight: lighter;
  user-select: none;
}
.titulo-linea-registro-abi {
  content: '';
  display: block;
  width: 25px;
  height: 1px;
  margin-left: 10px;
  margin-right: 10px;
  background: #000 !important;
  position: relative;
  bottom: 2px;
}

.btn-registro-abi {
  display: flex;
  flex-direction: row;
  border-style: solid;
  margin: 5px;
  width: 200px;
  border-width: 1px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

.btn-google-registro-abi {
  border-color: #d2afa9;
  background-color: #fff;
  color: #dc4436;
}
.btn-facebook-registro-abi {
  border-color: #4262ab;
  background-color: #fff;
  color: #4262ab;
}
.btn-container-principal-login {
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}
.btn-principal-registro-abi {
  background-color: #ffa300;
  color: #fff;
  font-weight: bold;
  padding: 10px 40px;
  border-radius: 5px;
}

.footer-registro-abi {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #efefef;
  align-self: flex-end;
  align-items: center;
}
.campos-container-registro-abi {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 80%;
}

.campo-registro-abi {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
}
.toggle-container-registro-abi {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.toggle-registro-abi {
  --background: #efefef;
  --background-checked: #efefef;
  --handle-background-checked: #28991f;
}
.resaltado-registro-abi {
  color: #ffa300;
  font-weight: bolder;
}
.input-items-container-registro {
  align-self: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.categorias-header {
  display: flex;
  justify-content: flex-start;
}

.margen-categoria-header {
  margin-left: 0px;
}
.mid-header-container {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-items: stretch;
  align-items: center;
}

@media screen and (min-device-width: 600px) {
  .margen-categoria-header {
    margin-left: 0px;
  }
  .mid-header-container {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    justify-items: stretch;
    align-items: center;
  }
}

.campo-registro-abi textarea {
  border-bottom: 1px solid #afafaf !important;
}

.modal-info-contrasena {
  --height: 345px !important;
}

.input-dot {
  font-size: 3rem;
  width: 25px;
  height: 40px;
  position: absolute;
  top: -5px;
  left: 10px;
  text-align: center;
  line-height: 2rem;
  animation: fadeIn 0.5s;
  -webkit-animation: fadeIn 0.5s;
  -moz-animation: fadeIn 0.5s;
  -o-animation: fadeIn 0.5s;
  -ms-animation: fadeIn 0.5s;
  border-bottom: 1px solid rgb(85, 85, 85);
}
