.sub-header-container {
  background-color: #f7f7f7;
  display: flex;
  flex-shrink: 0;
  z-index: 999;
}

.sub-header-container > div {
  height: 50px;
}

.link-sub-header {
  cursor: pointer;
}

.sub-header-container .link-sub-header > div {
  width: 40px;
}

.text-subheader-cliente {
  height: 2.5ex;
  margin-right: 5px;
  color: 'var(--gris-oscuro-abi)';
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.text-subheader-cliente {
  font-size: 15px;
  margin-right: 5px;
}

@media only screen and (max-width: 600px) {
  .text-subheader-cliente {
    font-size: 12px;
    margin-right: 5px;
  }
  .margen-subheader {
    overflow-x: scroll;
  }
}

@media only screen and (min-width: 600px) {
  .text-subheader-cliente {
    font-size: 15px;
    margin-right: 5px;
  }
}

.link-sub-header .svg-abi {
  width: 20px;
}
