.modal-info {
  --width: 70%;
  --max-width: 400px;
  --background: rgba(0, 0, 0, 0) !important;
  --box-shadow: none;
  border: 0 !important;
  box-shadow: none !important;
}

.modal-info .modal-wrapper {
  --box-shadow: none;
}

.modal-info .modal-wrapper .ion-page{
  justify-content: center;
} 

.modal-info .modal-wrapper,
.modal-info .modal-wrapper .ion-page {
  overflow: visible;
}

.header-modal-info {
  display: flex;
  justify-content: space-between;
  padding-left: 1rem;
  padding-right: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  color: #fff;
  font-weight: bolder;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
}

.modal-info-childen-container * {
  font-size: 1rem;
}

.header-modal-info span {
  font-size: 1rem;
}

.modal-info-childen-container {
  padding: 1rem;
  flex-grow: 1;
  height: 100%;
}

.modal-info-childen-container ul {
  padding-left: 15px;
}

@media screen and (max-width: 499px) {
  .modal-info-childen-container * {
    font-size: 1rem;
  }
}
@media screen and (min-width: 500px) {
}
