.custom-icon-abi.iniciar-oportunidad-icon > div {
  border: 0.6vw solid var(--naranja-transportista);
  border-radius: 100%;
  min-width: 70px;
  min-height: 70px;
  height: 19vw;
  width: 19vw;
  padding: 0.2vw;
  background-color: white;
  display: flex;
  justify-content: center;
}

.custom-icon-abi.iniciar-oportunidad-icon > div > div {
  border-radius: 100%;
  height: 100%;
  width: 100%;
  background-color: var(--naranja-transportista);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.custom-icon-abi.iniciar-oportunidad-icon > div > div * {
  color: white;
  font-weight: bolder;
  font-size: 0.7rem;
}
