.form-group > .form-control-container {
  color: #929292;
  border: 0;
  border-radius: 0;
  padding-left: 2px;
}

.form-control-container input.registro,
 div.form-control-container.registro,
.form-control-container textarea.registro{
  border-radius: 0;
  border-bottom: 1px solid #afafaf;
}

.icono_interno.form-control-container input.registro {
  border-radius: 0px;
  border-bottom: 0px solid #afafaf;
  margin-bottom: 0px;
}

.form-group .form-control-container .registro:focus {
  border-color: #555555;
  outline: 0 none;
  box-shadow: 0 0;
}

.form-group .registro {
  border: none;
}

.form-group .form-control-container .registro::placeholder {
  color: #afafaf;
  opacity: 1;
}

.registro-phone-input {
  border: 0 !important;
  border-radius: 0 !important;
  border-bottom: 2px solid rgb(224, 224, 224) !important;
}

.registro-phone-input::placeholder {
  color: #afafaf !important;
  opacity: 1 !important;
}

.registro-phone-input:focus {
  border-color: #555555 !important;
  outline: 0 none !important;
  box-shadow: 0 0 !important;
}

.titulo-externo-item-field {
  font-weight: bolder;
  color: var(--gris-oscuro-abi);
}

@media screen and (max-device-width: 499px) {
  .titulo-externo-item-field {
    font-size: 12px;
  }
}

.asyncselect div[class*="-placeholder"], .asyncselect div[class*="-input"] input {
  padding: 0.375rem 0.75rem;
  color: #afafaf;
}