
.perfil-transportista-list-container {
    height: 100%;
    overflow: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.perfil-transportista-list-container .item {
    color: #303AB2;
    display: flex;
    margin-top: 15px;
}

.perfil-transportista-list-container .item span {
    margin-left: 15px;
}

.nombre-cliente {
    font-weight: bolder;
    font-size: 1.2rem;
    color: var(--gris-oscuro)

}

.boton-calificar {
    background-color: var(--success);
    border-radius: 5px;
    height: 50px;
    display: flex;
    cursor: pointer;
    color: white;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
}

.box-info svg {
    position: absolute;
    left: 35px;
}

.calificacion-cliente-container{
    overflow: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
}
.calificacion-cliente-container [type="radio"]:checked + label {
    padding: 0;
    position: relative;
    cursor: pointer;
    display: inline-block;
    height: inherit;
    line-height: inherit;
    font-size: inherit;
    transition: .28s ease;
    -webkit-user-select: none;
    user-select: none;
}

.calificacion-cliente-container [type="radio"] + label:after {
    display: none;
    font-size: inherit;
}

.calificacion-cliente-container [type="radio"]:not(:checked) + label:before {
    display: none;
    border: 0 !important;
    font-size: inherit;
}

.calificacion-cliente-container [type="radio"]:not(:checked) + label {
    padding: 0;
    position: relative;
    cursor: pointer;
    display: inline-block;
    height: inherit;
    line-height: inherit;
    font-size: inherit;
    transition: .28s ease;
    -webkit-user-select: none;
    user-select: none;
}

.modal-calificacion {
    --height: 80%;
}

