#chat-interno-container {
  z-index: 1001;
  --borde-radio: 10px;
  --container-listado-width: 300px;
  position: fixed;
  bottom: 0px;
  display: flex;
  align-items: flex-end;
  gap: 1rem;
}

#chat-interno-container > * {
  z-index: 1002;
}

#chat-interno-container svg,
#chat-interno-container h4 {
  color: var(--gris-oscuro);
}

#chat-interno-container .listado-container,
#chat-interno-container .chat-interno-seleccionado {
  border-top-left-radius: var(--borde-radio);
  border-top-right-radius: var(--borde-radio);
  box-shadow: rgb(0 0 0 / 2%) 0px 1px 3px 0px, rgb(27 31 35 / 15%) 0px 0px 0px 1px;
  overflow: hidden;
}

#chat-interno-container .chat-interno-seleccionado {
  width: 350px;
}

#chat-interno-container .chat-interno-seleccionado .chat-component-container {
  height: 350px;
}

#chat-interno-container .listado-container,
#chat-interno-container .chat-interno-seleccionado {
  background-color: #fff;
}

#chat-interno-container .listado-container {
  width: var(--container-listado-width)
}

#chat-interno-container .listado-container .titulo,
#chat-interno-container .chat-interno-seleccionado .titulo {
  background-color: var(--gris-claro);
  cursor: pointer;
}

#chat-interno-container h4 {
  margin-bottom: 0;
}

#chat-interno-container .listado-container .lista {
  height: 300px;
}

#chat-interno-container .img-perfil-container,
#chat-interno-container .listado-container .icono {
  height: 30px;
  width: 30px;
  margin: 0 0.5rem;
}

#chat-interno-container .listado-container .icono path {
  fill: var(--naranja-abi) !important;
}

#chat-interno-container .img-perfil-container {
  border-radius: 100%;
  border: 1px solid var(--gris-oscuro);
  overflow: hidden;
}

#chat-interno-container .img-perfil-container img {
  object-fit: cover;
}

#chat-interno-container .chat-link {
  display: flex;
  align-items: center;
  overflow: hidden;
}

#chat-interno-container .chat-link .nombre {
  width: calc(var(--container-listado-width) - 100px);
  display: -webkit-box;
  white-space: nowrap;
  -webkit-box-direction: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

#chat-interno-container .search-container{ 
  padding-left: 0.4rem;
  padding-right: 0.4rem;
  height: 38px;
  margin-left: 0;
  border-radius: 2px;
  border: 1px solid var(--gris-claro);
}

#chat-interno-container .txt-dot {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
}

#chat-interno-container .txt-dot .dot {
  width: 15px;
  height: 15px;
  border-radius: 15px;
  background-color: #fe4b00;
  margin-right: 10px;
}
