#modal-selector-registro {
    --width: 1000px;
}

#modal-selector-registro .titulo {
    font-weight: bold;
    font-size: 2rem;
    color: var(--azul-abi);
    padding-top: 1.5rem;
}

#modal-selector-registro .subtitulo {
    font-weight: 400;
    font-size: 1.25rem;
    color: var(--gris-oscuro-abi);
}

#modal-selector-registro .modos {
    padding: 0 4rem;
}

#modal-selector-registro .titulo-container {
    text-align: center;
    padding: 1.25rem 0;
}

#modal-selector-registro .mfooter {
    color: var(--gris-oscuro-abi);
    text-align: center;
    font-weight: 400;
    padding: 1.25rem 0;
    background-color: #efefef;
}

#modal-selector-registro .login-btn {
    font-weight: bold;
    font-size: 1.25rem;
    color: var(--azul-abi);
    cursor: pointer;
}

#modal-selector-registro .btn-cerrar {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
}

#modal-selector-registro .ion-page {
    overflow-y: auto;
}

@media screen and (min-width: 501px) {
    #modal-selector-registro {
        --border-radius: 20px;
    }
}
