.modal-codigo-firebase {
  --background: white;
  --box-shadow: none;
  border: 0 !important;
  box-shadow: none !important;
}

.modal-codigo-firebase .titulo {
  font-size: 14px;
  margin: 100px 50px 50px 50px;
}

.modal-codigo-firebase .subtitulo {
  font-size: 14px;
  margin: 50px 60px 50px 60px;
}

.modal-codigo-firebase .boton-nuevo-codigo {
  font-size: 14px;
  font-weight: bolder;
}

.modal-codigo-firebase .boton-continuar {
  padding: 5px 65px 5px 65px;
}

.input-dot {
  font-size: 3rem;
  width: 25px;
  height: 40px;
  position: absolute;
  top: -5px;
  left: 10px;
  text-align: center;
  line-height: 2rem;
  animation: fadeIn 0.5s;
  -webkit-animation: fadeIn 0.5s;
  -moz-animation: fadeIn 0.5s;
  -o-animation: fadeIn 0.5s;
  -ms-animation: fadeIn 0.5s;
  border-bottom: 1px solid rgb(85, 85, 85);
}

/* Chrome, Safari, Edge, Opera */
.pin-container input::-webkit-outer-spin-button,
.pin-container input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.pin-container input[type=number] {
  -moz-appearance: textfield;
}

.pin-container {
  position: relative;
  margin: auto;
}

.pin-container .pin-input {
  width: 230px;
  letter-spacing: 21px;
  border: none;
  font-size: 22px;
  font-weight: bold;
  background: transparent;
  padding-left: 9px;
}

.pin-container .lineas {
  position: absolute;
  bottom: -6px;
  left: 4px;
  pointer-events: none;
}

.pin-container .lineas .linea {
  height: 1px;
  width: 25px;
  background: var(--gris-oscuro-abi);
  margin-right: 10px;
  display: inline-block;
}
